.Login {
    margin-right: 40px;
    color: #fff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    text-decoration: none;
    -webkit-transition: .2s color ease-out;
    -o-transition: .2s color ease-out;
    transition: .2s color ease-out;
}


.Login:hover {
    color: #f31f53;
    -webkit-transition: .2s color ease-in;
    -o-transition: .2s color ease-in;
    transition: .2s color ease-in;
}

.Login .icon, .Login .text {
    display: inline-block;
}

.Login .text {
    margin-left: 10px;
}

.Login .icon {
    width: 19px;
    height: 21px;
}

.Login .icon img {
    max-width: 100%;
    max-height: 100%;
}


